import { Component, h, Element, State, Host } from '@stencil/core';
import { ICellRendererComp } from 'ag-grid-community';
import { IIconCell, IIconCellParams } from '../../../../types/table-cells';

interface InitParams extends IIconCellParams {
    generateTooltip?: (params: string) => HTMLElement | string;
}

/**
 * @category Table Cell
 * @description A cell component for displaying an icon.
 */

@Component({
    tag: 'atui-icon-cell',
    shadow: false,
})
export class AtuiIconCell implements ICellRendererComp {
    @Element() el: HTMLElement;
    @State() params: InitParams;
    @State() icons: IIconCell[];

    init(params: InitParams): void {
        this.params = params;
        this.icons = params.value;
    }

    getGui(): HTMLElement {
        return this.el;
    }

    refresh(params: InitParams): boolean {
        this.updateCell(params.value);
        return true;
    }

    protected updateCell(newValue: any) {
        this.params.value = newValue;
        this.icons = this.params.value;
    }

    get getIcons(): any {
        return this.params.value.map((icon: IIconCell) => (
            <atui-tooltip
                position="right"
                is_visible={!!this.params.generateTooltip}
            >
                <div
                    class="flex items-center"
                    slot="tooltip-trigger"
                >
                    <span
                        class={`material-icons text-icon-md ${icon.iconClass}`}
                    >
                        {icon.iconName}
                    </span>
                </div>

                {this.params.generateTooltip && (
                    <span
                        slot="tooltip-content"
                        class={`leading-normal`}
                    >
                        {this.params.generateTooltip(icon.tooltipText)}
                    </span>
                )}
            </atui-tooltip>
        ));
    }

    render() {
        return (
            <Host class="flex h-full items-center overflow-hidden leading-[100%]">
                {this.getIcons}
            </Host>
        );
    }
}
